import "./menu.css";
import React from "react";
import SonhadorImg from "../../assets/sonhador.png";
import jwt_decode from "jwt-decode";
import { Link } from "react-router-dom";

export default function Menu() {
	const novoToken = localStorage.getItem("token");
	const tokenDecoded = jwt_decode(novoToken)
  return (
    <div className="Menu">
      <div className="menu-container">
        <div className="top">
          <img
            src={tokenDecoded.picture ? tokenDecoded.picture : SonhadorImg}
            alt="imagem do perfil do sonhador"
          />

          <span>Olá,</span>
          {
          }
          <span>{tokenDecoded.name}</span>
        </div>

        <a href={`${process.env.PUBLIC_URL}/como-funciona`}>Como funciona?</a>
		<Link to='/'><span className="logout" onClick={() => {
            localStorage.removeItem('token');
          }}>Sair</span></Link>
        
      </div>
    </div>
  );
}
