import React from "react";
import "./loading.css";
import { fazerRequisicaoComBody } from "../../utils/fetch";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSpinner } from "@fortawesome/free-solid-svg-icons";
import { faCheck } from "@fortawesome/free-solid-svg-icons";

export default function Loading(props) {
  const { cotas, hideModal } = props;
  const [loading, setLoading] = React.useState(false);

  const sendCall = () => {
    setLoading(true);

    if (cotas) {
      fazerRequisicaoComBody("url", "PUT", cotas)
        .then((res) => res.json())
        .then((resJson) => {
          console.log(resJson);
          setLoading(false);
        });
    } else {
      // chamar endpoint do back q mandar email de recuperação de senha
    }
  };

  React.useEffect(() => {
    sendCall();
  }, []);

  return (
    <div className="Loading">
      <div className="close">
        <button onClick={hideModal}>X</button>
      </div>
      {loading === true ? (
        <div>
          <FontAwesomeIcon icon={faSpinner} size="4x" pulse />
          <h2>Um momento</h2>
          {cotas ? (
            <span>O seu apoio está sendo registrado</span>
          ) : (
            <span></span>
          )}
        </div>
      ) : (
        <div>
          <FontAwesomeIcon icon={faCheck} size="4x" />
          <h2>Sucesso</h2>

          {cotas ? (
            <>
              <span>O seu apoio foi registrado.</span>
              <span>Nossa equipe entrará em contato.</span>
            </>
          ) : (
            <span>
              O e-mail de recuperação de senha foi enviado com sucesso
            </span>
          )}
        </div>
      )}
    </div>
  );
}
