import "./support-dream.css";
import React, { useRef } from "react";
import Header from "../../componentes/header/header";
import Footer from "../../componentes/footer/footer";
import SonhadorImg from "../../assets/sonhador-big.png";
import Menos from "../../assets/menos.png";
import Mais from "../../assets/mais.png";
import Modal from "react-modal";
import Tip from "../../componentes/tip/tip";

import Loading from "../../componentes/loading/loading";
import Quotas from "../../componentes/quotas/quotas";

export default function ApoiarSonho() {
  const [modalIsOpen, setModalIsOpen] = React.useState(false);

  const [loadingModalIsOpen, setLoadingModalIsOpen] = React.useState(false);
  const [clicked, setClicked] = React.useState(false);

  const [cotas, setCotas] = React.useState(0);

  const inputRef = useRef("");

  const showModal = () => {
    setModalIsOpen(true);
  };

  const hideModal = () => {
    setModalIsOpen(false);
  };

  const showLoading = () => {
    console.log("i hate this sometimes");
    setLoadingModalIsOpen(true);
  };

  const hideLoading = () => {
    console.log("i hate this always");
    setLoadingModalIsOpen(false);
  };

  return (
    <div className="SupportDream">
      <Header h1="" h2="" menu="true" arrow="true" />
      <div className="dreamer-img">
        <img src={SonhadorImg} alt="imagem de perfil sonhador" />
      </div>

      <div className="dream-content">
        <div className="dreamer">
          <h2>Marcelo Marmelo</h2>
          <div className="data">
            <span>Cota: R$200,00</span>
            <span>Baixo risco</span>
          </div>
          <hr></hr>
          <span className="note">
            As cotas que serão reservadas neste momento serão registradas como
            interesse.
          </span>
          <div className="data">
            <span>7 cotas disponíveis</span>
            <span>27 dias restantes</span>
          </div>
          <Modal
            hideModal={hideModal}
            isOpen={modalIsOpen}
            className="ModalTip"
            overlayClassName="OverlayFirstTime"
          >
            <Tip />
          </Modal>
          <Quotas />
          {
            // passar o quotasquantity e quotas quando integrar
            // na vdd tem que passar o cotas (state definido nesse arquivo) para quando o user clicar no + e no - mudar as classes do componente
            // eu acho? >.>
          }
          <span>Adicione quantas cotas você deseja apoiar</span>
          <div className="quotas-number">
            <div
              className="more-less"
              onClick={() => {
                let novaCota = cotas - 1;
                if (novaCota >= 0 && novaCota <= 10) {
                  setCotas(novaCota);
                  inputRef.current.value = novaCota;
                  // trocar 10 por total de cotas
                  let percent = 10 * (25 / 100);
                  if (cotas >= percent && clicked === false) {
                    showModal();
                    setClicked(true);
                  }
                }
              }}
            >
              <img src={Menos} alt="imagem para diminuir o número de cotas" />
            </div>
            <input ref={inputRef} type="text"></input>
            <div
              className="more-less"
              onClick={() => {
                let novaCota = cotas + 1;
                console.log(novaCota);
                if (novaCota >= 0 && novaCota <= 10) {
                  setCotas(novaCota);
                  inputRef.current.value = novaCota;
                }
              }}
            >
              <img src={Mais} alt="imagem para aumentar o número de cotas" />
            </div>
          </div>
          <span className="wished">Quero apoiar com {cotas} cotas</span>
          <span className="total">Total R$0,00</span>
          <button
            onClick={() => {
              // fazerRequisicaoComBody("url", "PUT", cotas);
              showLoading();

              //mandar call pro back com o apoio
              // o que acontece exatamente quando o apoio é feito? uma página de sucesso?
              // fazerRequisicaoComBody("url", "PUT", cotas);
            }}
          >
            Apoiar este sonho
          </button>
          <Modal
            hideModal={hideLoading}
            isOpen={loadingModalIsOpen}
            className="ModalLoading"
            overlayClassName="OverlayFirstTime"
          >
            <Loading cotas={cotas} />
          </Modal>
        </div>
      </div>

      <Footer />
    </div>
  );
}
