/* eslint-disable import/no-anonymous-default-export */
import axios from "axios";

export function fazerRequisicaoComBody(url, metodo, conteudo) {
  return fetch(url, {
    method: metodo,
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify(conteudo),
  });
}

let API = axios.create({
  baseURL: process.env.REACT_APP_API_BASE_ENDPOINT,
  headers: { Authorization: "Bearer " + localStorage.getItem("token") },
});

export default {
  updateAuthorization: (token) => {
    localStorage.setItem("token", token);

    API = axios.create({
      baseURL: process.env.REACT_APP_API_BASE_ENDPOINT,
      headers: { Authorization: "Bearer " + localStorage.getItem("token") },
    });
  },

  getToken: () => localStorage.getItem("token"),

  auth: (email, password) =>
    API.post("auth", {
      email,
      password,
    }),

  supporter: (
    name,
    birthdate,
    phoneNumber,
    cep,
    cpf,
    email,
    password,
    hasInvestingExperience,
    investingType,
    investingBudget,
    type
  ) =>
    API.post("supporter", {
      name,
      birthdate,
      phoneNumber,
      cep,
      cpf,
      email,
      password,
      hasInvestingExperience,
      investingType,
      investingBudget,
      type,
    }),

  dreamer: (name, birthdate, phoneNumber, cep, cpf, email, password, changepassword) =>
    API.post("dreamer", {
      name,
      birthdate,
      phoneNumber,
      cep,
      cpf,
      email,
      password,
	  changepassword,	  
    }),

  registerDream: (
    question1_status,
    question2_status,
    question3_status,
    question4_status,
    question5_status,
    question6_status,
    estimatedCashGoal,
    description,
    type
  ) =>
    API.post("dreams", {
      question1_status,
      question2_status,
      question3_status,
      question4_status,
      question5_status,
      question6_status,
      estimatedCashGoal,
      description,
      type,
    }),

	dreams: (offset) =>
	API.get(`dreams?offset=${offset}`),

	dreamById: (id) =>
	API.get(`dreams/${id}`),

	emailConfirmation: (token) => API.get(`/confirmation/${token}`),
};
